.App {
  padding: 0;
  margin: 0;
  height: 200vh;
}

.App-header {
  background-color: #000000;
  position: absolute;
  top: 54px;
  width: 100%;
  height: 54px;
  display: flex;
  font-size: 0;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header::nth-child(2) {
  margin: 5px;
}

.App-banner-invisible {
  visibility: hidden;
}

.App-banner {
  overflow: hidden;
  background: url('./assets/image/00001.png') -35px -30px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.896);
  border-radius: 16px;
  display: flex;
  justify-content: flex-end;
  transform: translateX(-150%);
  animation: ani 1.5s forwards;
  height: 330px;
  min-width: 565px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  color: white;
  padding: 10px;
}

.App-banner-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
}

h1 {
  margin: 0;
}

h3{ 
  font-size: 40px;
  margin: 0;
  background: linear-gradient(45deg, rgba(125, 83, 23, 1), rgba(237, 223, 149, 1));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
}

@keyframes ani {
  0% {transform: translateX(-150%);}
  100% {transform: translateY(0);}
}

.App-banner-button {
  background-color: transparent;
  border: none;
  position: absolute;
  color: #fff;
  right: 20px;
  top: 20px;
}
